body {
  background-color: #f6f6f6;
}

.shopping-list-wrapper {
  justify-content: center;
  display: flex;
}

.sl-cont {
  padding-top: 60px;
}

.sl-title {
  letter-spacing: -.04em;
  color: #111;
  color: var(--title-text-color-theme-light);
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.empty-cart {
  width: 265px;
  margin-left: auto;
  margin-right: auto;
}

.sl-part {
  color: var(--accent-color);
}

.sl-list > li {
  max-width: 335px;
  margin-bottom: 20px;
}

.trash-btn {
  width: 28px;
  height: 28px;
  background: var(--accent-color);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  padding: 12px;
  position: relative;
  top: 14px;
  right: 14px;
}

.trash-btn > svg {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sl-card {
  height: 170px;
  width: 100%;
  background-color: var(--header-bg-color-theme-light);
  opacity: 1;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  transition: opacity .5s ease-in-out;
  display: flex;
}

.sl-card.removing {
  opacity: 0;
}

.sl-product-img {
  border-radius: 8px;
  margin: 14px;
}

.sl-product-cat {
  letter-spacing: .03em;
  color: var(--secondary-text-color);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.sl-product-title {
  max-width: 170px;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--title-text-color-theme-light);
  margin-top: 14px;
  margin-bottom: 4px;
  overflow: hidden;
}

.sl-product-descr {
  height: 68px;
  letter-spacing: -.04em;
  color: var(--title-text-color-theme-light);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  overflow-y: scroll;
}

.sl-product-descr::-webkit-scrollbar {
  width: 8px;
}

.sl-product-descr::-webkit-scrollbar-thumb {
  background-color: var(--secondary-text-color);
  height: 4px;
  border-radius: 12px;
}

.sl-product-author {
  letter-spacing: -.04em;
  color: var(--secondary-text-color);
  letter-spacing: -.04em;
  color: var(--secondary-text-color);
  letter-spacing: -.04em;
  color: #b4afaf;
  max-width: 90px;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: auto;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.16;
  overflow: hidden;
}

.sl-aut-shops, .sl-eshops {
  justify-content: center;
  align-items: center;
  display: flex;
}

.sl-eshops > li:not(:last-child) {
  margin-right: 8px;
}

.sl-message {
  text-align: center;
  letter-spacing: -.02em;
  color: rgba(0, 0, 0, .6);
  margin-bottom: 14px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
}

@media screen and (min-width: 768px) {
  .sl-list > li {
    max-width: 704px;
  }

  .sl-card {
    height: 213px;
  }

  .sl-product-img {
    margin: 24px;
  }

  .sl-product-title {
    width: auto;
    margin-top: 24px;
    overflow: visible;
  }

  .sl-product-cat {
    margin-bottom: 14px;
  }

  .sl-product-descr {
    margin-bottom: 20px;
    padding: 0;
  }

  .sl-product-author {
    overflow: visible;
  }

  .trash-btn {
    width: 34px;
    height: 34px;
    top: 24px;
    right: 24px;
  }

  .trash-btn > svg {
    width: 18px;
    height: 18px;
  }

  .sl-product-info {
    width: inherit;
  }

  .sl-message {
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 1.33;
  }

  .sl-eshops > li:not(:last-child) {
    margin-right: 16px;
  }
}

@media screen and (min-width: 1440px) {
  .sl-list > li {
    max-width: 996px;
  }
}

